*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root{
/*colors*/
  /*Neutral colors*/
  ---neutral-white: #FFFFFF;

  /*CTA colors*/
  ---cta-base: #686D72;

/*font sizes*/
  --size-heading1: 3rem;

/*font-weight*/

/*line-height*/
--line-height-heading: -1.2;
}

h1{
  font-size: 4rem;
}
h1, h2, h3, h4, h5, h6{
  font-family: 'Roboto', sans-serif;
  line-height: var(--line-height-heading);
}

.container{
  height: 100vh;
}

.footer {
  background-color: var(---cta-base);
  color: var(---neutral-white);
  margin-top: auto; /* Ensures footer pushes to bottom */
}

.footer__container {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.footer__left {
  margin-bottom: 20px;
}

.footer__logo {
  width: 50%;
  margin-bottom: 10px;
}

.footer__text {
  font-size: 16px;
  margin-bottom: 8px;
}

.footer__email,
.footer__phone {
  text-decoration: underline 1px;
  font-size: 14px;
}

.footer__email a,
.footer__phone a {
  text-decoration: none;
}

.footer__right {
  display: flex;
  flex-direction: column;
}

.footer__column {
  margin-bottom: 20px;
}

.footer__column-heading {
  font-size: 18px;
  margin-bottom: 8px;
}

.footer__links {
  list-style-type: none;
}

.footer__links li {
  margin-bottom: 8px;
}

.footer__links a {
  text-decoration: none;
}


@media (min-width: 1024px) {
  .footer__container {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    max-width: 1440px;
    margin: 0 auto;
    padding: 40px;
  }

  .footer__left {
    text-align: left;
    width: 30%;
  }

  .footer__right {
    display: flex;
    justify-content: space-between;
    width: 65%;
  }

  .footer__column {
    width: 30%; 
    margin-bottom: 0;
  }
}


.footer__column-heading {
  font-size: 18px;
  margin-bottom: 8px;
}

.footer__links {
  list-style-type: none;
}

.footer__links li {
  margin-bottom: 8px;
}

.footer__links a {
  text-decoration: none;
}


@media (min-width: 1024px) {
  .footer__container {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    max-width: 1440px;
    margin: 0 auto;
    padding: 40px;
  }

  .footer__left {
    text-align: left;
