  }

  .footer__right {
    display: flex;
    justify-content: space-between;
    width: 65%;
  }

  .footer__column {
    width: 30%;
  }
}
/* General Header Styling */
.header {
  position: sticky;
  padding-left: 160px; 
  padding-right: 160px;
  padding-top: 10px;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.header__logo img {
  width: 190px;
}

.header__mobile-toggle {
  display: none;
  background: none;
  border: none;
  cursor: pointer;
}

.header__mobile-icon {
  width: 25px;
  height: 2px;
  background-color: #333;
  position: relative;
}

.header__mobile-icon::before,
.header__mobile-icon::after {
  content: "";
  width: 25px;
  height: 2px;
  background-color: #333;
  position: absolute;
  left: 0;
}

.header__mobile-icon::before {
  top: -6px;
}

.header__mobile-icon::after {
  top: 6px;
}

/* Navbar */
.header__nav {
  display: flex;
  justify-content: flex-end; /* Align items to the right */
  width: 100%;
}

.header__nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.header__nav-item {
  margin-left: 20px;
}

.header__nav-link {
  text-decoration: none;
  color: #333;
  font-size: 16px;
  display: flex;
  align-items: center;
}

.header__nav-item--dropdown .header__dropdown-toggle {
  display: flex;
  align-items: center;
  border: none;
  background: none;
  cursor: pointer;
}

.header__dropdown-menu {
  list-style: none;
  margin: 0;
  padding: 0;
  display: none;
  position: absolute;
  background-color: #fff;
  border: 1px solid #ccc;
}

.header__nav-item--dropdown:hover .header__dropdown-menu {
  display: block;
}

.header__dropdown-item {
  padding: 10px;
  text-decoration: none;
  color: #333;
  display: block;
}

.header__nav-item--search .header__search-toggle {
  background: none;
  border: none;
  cursor: pointer;
}

.header__nav-link--login {
  font-weight: bold;
}

/* Mobile Specific */
@media (max-width: 768px) {
  .header {
    padding-left: 20px; /* Smaller padding on mobile */
    padding-right: 20px; /* Smaller padding on mobile */
  }

  .header__nav {
    display: none;
    width: 100%;
  }

  .header__nav-list {
    flex-direction: column;
    width: 100%;
  }

  .header__nav-item {
    margin: 10px 0;
  }

  .header__mobile-toggle {
    display: block;
  }

  .header__mobile-toggle.active + .header__nav {
    display: block;
  }
}
